.carousel-item img {

    width: 100%;
    height: auto;
}
.navbar-dark .navbar-nav .nav-link {
    /* color: rgba(255,255,255,.55); */
    display: inline;
}
.container-fluid {
    padding: 0 !important;
}
.carousel-control-next-icon {
    border: 3px solid white;
    padding: 20px;
}
.carousel-control-prev-icon {
    border: 3px solid white;
    padding: 20px;
}
.card img {
    border-radius: 50%;
}
@media (min-width: 1200px) {
    h3,
    h3 {
        font-size: 3.75rem!important;
    }
    .carousel-caption {
        bottom: 15.25rem!important;
    }
}
.carousel-caption h3 {
  color: black;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}
.carousel-caption p {
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 22px!important;
    color: black;
    -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}
.img-sec {
    position: relative;
}
.overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    cursor: pointer;
}
.container1 {
    position: relative;
    width: 100%;
    overflow: hidden;
    padding-top: 56.25%;
    /* 16:9 Aspect Ratio */
}

.responsive-iframe {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    border: none;
}
.close {
    position: absolute;
    right: 0;
    z-index: 999;

}
.modal {
    position: relative;
}


.columns {
    float: left;
    width: 33.3%;
    padding: 8px;
}

.price {
    list-style-type: none;
    border: 1px solid #eee;
    margin: 0;
    color: white;
    padding: 0;
    transition: 0.3s;
}
.price li:nth-child(2) {
    color: #111;
}
.price:hover {
    box-shadow: 0 8px 12px 0 #eee;
}

.price .header {
    background-color: #111;
    color: white;
    font-size: 25px;
}

.price li {
    border-bottom: 1px solid #eee;
    padding: 20px;
    text-align: center;
}

.price .grey {
    background-color: #eee;
    font-size: 20px;
}

.button {
    background-color: #04AA6D;
    border: none;
    color: white;
    padding: 10px 25px;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
}
@media only screen and (max-width: 600px) {
    .columns {
        width: 100%;
    }
}
.columns:hover{
  animation: mymove 1s ease-in-out infinite alternate-reverse both;
  cursor: pointer;
}
.card:hover{
  animation: mymove 0.5s ease-in-out infinite alternate-reverse both;
  cursor: pointer;
}
.img-sec:hover{
  -webkit-animation: none;
          animation: none;
}
.img-sec{
  animation: mymove 0.5s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes mymove {
  0% {-webkit-transform: translateY(5px);transform: translateY(5px);}
  100% {-webkit-transform: translateY(-5px);transform: translateY(-5px);}
}
@keyframes mymove {
  0% {-webkit-transform: translateY(5px);transform: translateY(5px);}
  100% {-webkit-transform: translateY(-5px);transform: translateY(-5px);}
}
